<template>
    <div class="customers container">
        <alert v-if="alertMsg" :message="alertMsg"></alert>
        <h1 class="page-header">用户管理系统</h1>
        <input type="text" class="form-control" placeholder="搜索">
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>姓名</th>
                    <th>电话</th>
                    <th>邮箱</th>
                    <th>经历</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="customer in customers" :key="customer.id">
                    <td>{{customer.name}}</td>
                    <td>{{customer.phone}}</td>
                    <td>{{customer.email}}</td>
                    <td>
                        <router-link :to="/experience/ + customer.id">{{customer.profile | sliceExperience()}}</router-link>
                        <!--调用的时候不用写参数 可以直接把参数拿过来
 -->
                    </td>
                    <td>
                        <router-link :to="'/customersDetail/' + customer.id" class="btn btn-primary">详情</router-link>
                    </td>
                </tr>
            </tbody>
        </table> 
    </div>
</template>
<script>
import Alert from './Alert'
export default {
    name : "customers",
    data () {
        return {
           customers : [],
           alertMsg : "" 
        }
    },
    components: {
        Alert
    },
    filters: {
        sliceExperience (value) {
            return value.slice(0,3) + "...";
        }
    },
    methods: {
        fetchCustomers () {
            if(this.$route.query.alert) {
                this.alertMsg = this.$route.query.alert;
            }
            this.$http.get("http://localhost:3000/users")
                .then( (res) => {
                    this.customers = res.data;
                    console.log(this.customers)
                });
        }
    },
    created () {
        this.fetchCustomers();
    },
    // updated () {
    //     this.fetchCustomers();
    // }

}
</script>
<style scoped>
thead a{
    color:black;
}
</style>




