<template>
    <div class="experience container">
        <router-link to="/" class="btn btn-success">返回</router-link>
        <div class="panel panel-default">
            <div class="panel-body">
                {{experience}}
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name : "experience",
    data () {
        return {
            experience : ""
        }
    },
    methods: {
        fetchExperience () {
            this.$http.get("http://localhost:3000/users/" + this.$route.params.id)
                .then( res => this.experience = res.data.profile);
        }
    },
    created () {
        this.fetchExperience();
    }
}
</script>



