<template>
    <div class="add container">
        <router-link class="btn btn-success" to="/">返回</router-link>
        <alert :message="noAddAlert" v-if="noAddAlert"></alert>
        <form>
        <div class="well">
            <h4>用户信息</h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name">
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email">
            </div>
            <div class="form-group">
                <label>经历</label>
                <textarea rows="10" class="form-control" v-model="customer.profile">

                </textarea>
            </div>
        </div>
        <input type="button" value="提交" @click="submitted" class="btn btn-success">
        </form>
    </div>
</template>
<script>
import Alert from './Alert'
export default {
    name : "add",
    data () {
        return {
            customer : {},
            noAddAlert : ""
        }
    },
    components: {
        Alert
    },
    methods: {
        submitted () {
            if(!this.customer.name || !this.customer.phone || !this.customer.email || !this.customer.profile) {
                this.noAddAlert = "信息没有填写完毕哦~";
            }else{
                this.$http.post("http://localhost:3000/users",this.customer)
                .then( (res) => {
                    this.$router.push({
                        path : "/",
                        query : {
                            alert : "用户信息添加成功"
                        }
                    })
                });
            }
            
        }
    }
}
</script>



