<template>
    <div class="edit container">
        <alert :message="noEditMsg" v-if="noEditMsg"></alert>
        <router-link  to="/" class="btn btn-success">返回</router-link>
        <div class="well">
            <h4>
                编辑信息
            </h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name">
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email">
            </div>
            <div class="form-group">
                <label>经历</label>
                <textarea rows="10" class="form-control" v-model="customer.profile">

                </textarea>
            </div>
            <button class="btn btn-success" @click.prevent="submitted">编辑</button>
        </div>
    </div>
</template>
<script>
import Alert from './Alert'
export default {
    name : "edit",
    data () {
        return {
            customer : {},
            id : this.$route.params.id,
            noEditMsg : ""
        }
    },
    components: {
        Alert
    },
    methods: {
        fetchCustomer () {
            this.$http.get("http://localhost:3000/users/" + this.id)
                .then( res => this.customer = res.data);
        },
        submitted () {
            if(!this.customer.name || !this.customer.phone || !this.customer.email || !this.customer.profile) {
                this.noEditMsg = "请输入更新后的值~";
            }else{
            this.$http.put("http://localhost:3000/users/" + this.id,this.customer)
                .then( res => {
                    this.$router.push({
                        path : "/",
                        query : {
                            alert : "用户信息更新成功"
                        }
                    })
                });
            }
        }
    },
    created () {
        this.fetchCustomer();
    }
}
</script>



