import Vue from 'vue'
import Router from 'vue-router'
import Customers from '../components/Customers.vue'
import About from '../components/About.vue'
import Add from '../components/Add.vue'
import NoTouch from '../components/NoTouch'
Vue.use(Router)

// 路由模板 模板是同一个，根据穿进去的不同id拿到不同的数据，渲染不同的页面
import CustomersDetail from '../components/CustomersDetails'
import Edit from '../components/Edit.vue'
import Experience from '../components/Experience.vue'
export default new Router({
  mode : "history",
  routes: [
    {
      path : "/",
      component : Customers
    },
    {
      path : '/about',
      component : About
    },
    {
      path : '/add',
      component : Add
    },
    {
      path : '/notouch',
      component : NoTouch
    },
    {
      path : "/customersDetail/:id",
      component : CustomersDetail
    },
    {
      path : "/edit/:id",
      component : Edit
    },
    {
      path : "/experience/:id",
      component : Experience
    },
    {
      path : "*",
      redirect : "/"
    }
  ]
})



