<template>
    <div class="customersdetails">
        <router-link class="btn btn-success" to="/">返回</router-link>
        <form>
        <div class="well">
            <h4>
                用户信息
                <span class="pull-right">
                    <router-link :to="'/edit/' + id" class="btn btn-info">编辑</router-link>
                    <button class="btn btn-danger" @click.prevent="deleteCustomer">删除</button>
                </span>
            </h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name">
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email">
            </div>
            <div class="form-group">
                <label>经历</label>
                <textarea rows="10" class="form-control" v-model="customer.profile">

                </textarea>
            </div>
        </div>
        </form>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name : "customersdetails",
    data () {
        return {
            customer : {},
            id : this.$route.params.id,
        }
    },
    created () {
        this.fetchCustomers();
    },
    methods: {
        fetchCustomers () {
            this.$http.get("http://localhost:3000/users/" + this.id)
                .then( res => {
                    this.customer = res.data;
                })
        },
        deleteCustomer () {
            this.$http.delete("http://localhost:3000/users/" + this.id)
                 .then( res => {
                     this.$router.push(
                        {
                         path : '/',
                         query : {
                             alert : "用户信息删除成功！" 
                         }
                        }
                    )
                 });
        }
    },
    
}
</script>



